<script>
	let validateMoney = (rule, value, callback) => {
		let valuation = _.toNumber(value);
		if (_.isNaN(valuation)) {
			callback(new Error('金额应为数字'));
		} else {
			callback();
		}
	};
	import upLoad from '@/components/upload.vue'
	import imgList from '@/components/imglist.vue'
	export default {
		data () {
			return {
				upData:{
					aBaseName:'',
					loanId:'',
					serialNo:'',
					eProduct:-1
				},
				actionUrl:'/imagesDo/uploadImages',
				formItem: {
					loanMoney: '',
					loanMode: '',
					loanDate: '',
					outRemark:'',
				},
				ruleValidate: {
					loanMoney: [
						{required: true, message: '实际放款金额不能为空', trigger: 'blur'},
						{required: true, trigger: 'blur', validator: validateMoney}
					],
					loanMode: [
						{type: 'number', required: true, message: '请选择放款模式', trigger: 'change'}
					],
					loanDate: [
						{required: true, type: 'date', message: '选择实际放款日期', trigger: 'change'}
					]
				},
				submitNow: false,
				refreshFlag:false,
				loanInfo: {},
				upList:[],
				logList:[]
			}
		},
		components:{
			upLoad,imgList
		},
		created: function () {
			this.baseInfo = JSON.parse(sessionStorage.getItem('baseInfo'));
			this.loadInfo()
		},
		methods: {
			loadInfo: function (getNew) {
				let _this = this;
				let params = _this.$route.params;
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				_this.refreshFlag=true;
				_this.$http.post('loanManage/applicantDetails', {
					loanId:getNew?0:params.loanId,
					serialNo:params.serialNo
				})
					.then(function (response) {
						let res = response.data;
						_this.refreshFlag=false;
						if (res.code == '1') {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.loanInfo = res.data;
							_this.$Message.success({
								content: res.msg
							});
							_this.upData.loanId=res.data.loanId;
							_this.upData.serialNo=res.data.serialNo;
							_this.upData.aBaseName=res.data.fileRPath.loan;
							_this.$nextTick(function () {
								let index=parseFloat(res.data.logViewList.length)-1;
								_this.logList=['panel'+index];
							})
						} else {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.$router.go(-1);
							_this.$Notice.error({
								title: res.msg,
								duration: 3
							});
						}
					})
					.catch(function (error) {
						//处理错误
						clearTimeout(timer);
						_this.$Message.destroy();
						_this.refreshFlag=false;
						_this.$router.go(-1);
						_this.$Notice.error({
							title: error,
							duration: 3
						});
					});
			},
			handleUpLoad:function(list){
				this.upList=list;
			},
			submit: function (name) {
				let _this = this;
				_this.$refs[name].validate((valid) => {
					if (valid) {
						_this.submitNow = true;
						_this.$Modal.confirm({
							title: '账务确认',
							content: '<p>点击确定就将放款，是否确定?</p>',
							loading: true,
							onOk: () => {
								let params=_this.$route.params;
								_this.$http.post('loanManage/alterAuditAffirm', {
									loanId: params.loanId,
									flowStatus: 1,
									nodeStatus:params.nodeStatus,
									startTime: new Date(_this.formItem.loanDate).Format('yyyy-MM-dd'),
									eProduct: _this.formItem.loanMode,
									realLoanMoney:_this.formItem.loanMoney,
									outRemark:_this.formItem.outRemark,
									loanVoucher:JSON.stringify(_this.upList),
									serialNo:params.serialNo
								})
									.then(function (response) {
										if (response.data.code == 1) {
											_this.$Modal.remove();
											_this.$Message.success({
												content: response.data.msg,
												onClose: function () {
													_this.$router.go(-1);
												}
											});
										} else {
											_this.$Message.error({
												content: response.data.msg,
												duration: 3,
												onClose: function () {
													_this.$Modal.remove();
													_this.submitNow = false;
												}
											});
										}
									})
									.catch(function (error) {
										_this.$Message.error({
											content: error,
											duration: 3,
											onClose: function () {
												_this.$Modal.remove();
												_this.submitNow = false;
											}
										});
									});
							},
							onCancel: function () {
								_this.submitNow = false;
							}
						});
					} else {
						this.$Message.error('表单验证失败，请检查！');
					}
				})

			},
		}
	}
</script>
